.Card {
  width: 80%;
  border-bottom: 0.013333rem solid #000;
  padding: 1rem 0;
  cursor: default;
}
.Card .dsc {
  padding: 1rem 0;
  overflow: hidden;
}
.Card .go {
  font-size: 0.666667rem;
  cursor: pointer;
  padding-left: 1rem;
}
.Card .go,
.Card .go:link {
  color: #551a8b;
}
.Card:last-child {
  border: none;
}